.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 1200;
  }
  
  .modal {
    padding: 20px;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    width: 370px;
    /* height: calc(100vh - 24px); */
    background-color: rgb(98, 230, 98);
  }
  
  
  .button {
    margin-top: 30px;
    padding: 8px 16px;
    border-radius: 4px;
    background-color: #f0f8ff;
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
    text-align: center;
    display: inline-block;
    color: #000;
    border: 0;
    text-decoration: none;
    cursor: pointer;
    font-family: inherit;
    font-size: 18px;
    line-height: 24px;
    font-style: normal;
    font-weight: 500;
    min-width: 180px;
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
      0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
  }
  
  .button:hover,
  .button:focus {
    background-color: #cccccc;
  }
  .form{
    display: flex;
    flex-direction: column;
  }
  .label{
    font-weight: 500;
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
  }
  .text{
    font-weight: 700;
    text-align: center;
  }
  .input{
    border: 2px solid #000;
    border-radius: 5px;
  }