.label{
    display: flex;
    flex-direction: column;
}
.input{
    margin-top: 10px;
    border-radius: 4px;
    width: 370px;
}
.input:focus{
    border-color: rgb(29, 159, 229);
    border-width: 2px;
    outline:none;
    box-shadow: 0 1px 5px rgb(41, 152, 212);
}
.text{
    text-align: center;
    font-weight: 700;
    font-size: x-large;
}